body {
  width: 800px;
  height: 480px;
  overflow: hidden;
}

.title {
  font-size: 18px;
  font-weight: 500;
}

.sub {
  font-size: 16px
}

.chartContainer {
  padding: 5px;
  display: inline;
  width: 200px;
}

.statsContainer {
  border: 1px solid grey;
  border-radius: 5px;
  flex: auto;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 2px;
}

.allContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.tempContainer {
  width: 100px;
}